/* eslint-disable */ 
<template>
  <div class="container">
    <canvas id="bg">

    </canvas>
  </div>
</template>

<script>
import * as THREE from 'three';
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';


  import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
  import { DRACOLoader } from 'three/addons/loaders/DRACOLoader.js';

export default {
  name: 'App',
  mounted() {
    const scene = new THREE.Scene();

    const geometry = new THREE.BoxGeometry( 1, 2, 1 );
			const material = new THREE.MeshStandardMaterial( { color: 0xff0000} );
			const cube = new THREE.Mesh( geometry, material );
			scene.add( cube );
      const robot = THREE.use

      const pointLight = new THREE.PointLight(0xfffffff);
      pointLight.position.set(0, 0, 20);
      const pointLight2 = new THREE.PointLight(0xfffffff);
      pointLight2.position.set(5, 10, 0);
      pointLight.position.set(0, 0, 20);
      const pointLight3 = new THREE.PointLight(0xfffffff);
      pointLight3.position.set(-5, -10, -10);
      scene.add(pointLight)
      scene.add(pointLight2)
      scene.add(pointLight3)

      const ambientLight = new THREE.AmbientLight(0xffffff);
      ambientLight.position.set(0,0, 20);
      const lightHelper = new THREE.PointLightHelper(pointLight);
      const gridHelper = new THREE.GridHelper(200, 50);
      // scene.add(lightHelper, gridHelper)

    const camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );

    const renderer = new THREE.WebGLRenderer({
      canvas: document.querySelector('#bg'),
    });

      const controls = new OrbitControls( camera, renderer.domElement );

			camera.position.z = 5;

      // const spaceTexture = new THREE.TextureLoader().load('/src/assets/space.jpg');
      // scene.background = spaceTexture;

			function animate() {
				requestAnimationFrame( animate );
        
      controls.update();

				renderer.render( scene, camera );
			}

      // const dracoLoader = new DRACOLoader();
			// dracoLoader.setDecoderPath( 'jsm/libs/draco/gltf/' );

			const loader = new GLTFLoader();
			// loader.setDRACOLoader( dracoLoader );
			loader.load( 'robot.glb', function ( gltf ) {
				const model = gltf.scene;
				model.position.set( 0, 1, 0 );
				model.scale.set( 0.5, 0.5, 0.5 );
				scene.add( model );

				// mixer = new THREE.AnimationMixer( model );
				// mixer.clipAction( gltf.animations[ 0 ] ).play();

				animate();

			}, undefined, function ( e ) {

				console.error( e );

			} );

			// animate();

    renderer.setSize( window.innerWidth, window.innerHeight );
  },
}
</script>

<style>
#bg {
  width: 800px !important;
  height: 900px !important;
  position: absolute;
  z-index: 10;
}
